header {
	width: 100%;
	height: 0px; //
	box-shadow: 0 0 16px 3px rgba(0 0 0 / 15%);
	opacity: 0; //
	transition: 1s;
	z-index: 3;
	.inner {
		width: 1180px;
		height: 0px;
		margin: 0 auto;
		position: relative;
		display: flex;
		bottom: 20px; //
		justify-content: space-between;
		opacity: 0; //
		transition: 1s;
		h1 {
			a {
				.fa-discord {
					font-size: 50px;
					color: #6a88d8;
				}
			}
		}
		.menuWeb {
			display: flex;

			#gnb {
				li {
					float: left;
					a {
						display: block;
						padding: 14px 40px;
						font: bold 16px/1 'arial';
						letter-spacing: 0px;
						color: #6a88d8;
					}
					&:hover {
						a {
							&::after {
								content: '';
								display: block;
								box-shadow: 0 0 16px 5px
									rgba($color: #282c37, $alpha: 10%);
							}
						}
					}
				}
			}
		}
		.btnCall {
			width: 30px;
			height: 30px;
			position: absolute;
			top: 50%;
			left: 20px;
			transform: translateY(-20%);
			display: none;
			.fa-bars {
				font-size: 30px;
				color: #000;
				cursor: pointer;
			}
		}

		.menuMo {
			width: 270px;
			height: 100vh;
			background-color: #6a88d8;
			position: fixed;
			top: 0;
			padding: 35px 30px;
			z-index: 10;
			text-align: center;
			h2 {
				padding-bottom: 30px;
				border-bottom: 1px solid #fff;
				> a {
					.fa-discord {
						font-size: 50px;
						color: #fff;
					}
				}
				img {
					width: 20px;
					height: 20px;
					position: absolute;
					right: 10px;
					top: 10px;
					cursor: pointer;
				}
			}
			#gnbMo {
				li {
					a {
						display: block;
						width: 100%;
						padding: 20px 0;
						border-bottom: 1px solid #fff;
						font: bold 14px/1 'arial';
						color: #fff;
					}
				}
			}
			> p {
				color: #fff;
				position: absolute;
				bottom: 40px;
				font-size: 15px;
			}
			&.on {
				left: 0px;
			}
		}
	}

	&.on {
		height: 80px;
		opacity: 1;
		.inner {
			opacity: 1;
			bottom: -20px;
		}
	}
}

@media screen and (max-width: 1179px) {
	header {
		.inner {
			width: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			h1 {
				a {
					.fa-discord {
						font-size: 40px;
						margin-top: 50%;
					}
				}
			}
			.menuWeb {
				display: none;
			}

			.btnCall {
				display: block;
			}

			.menuMo {
				display: block;
			}
		}
		&.on {
			height: 60px;
		}
	}
}
