.btnsBox {
    position: fixed;
    bottom: 30px;
    right: 54px;
    text-align: left;
    z-index: 100;
    display: flex;
    flex-direction: column;
    gap: 10px;
    .up {
        position: relative;
        background-color: #000;
        width: 52px;
        height: 0px;
        opacity: 0;
        border-radius: 50%;
        border: 1px solid #000;
        box-shadow: 0px 10px 15px 0 rgba(0, 0, 0, 0.25);
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: 0.5s;
        .fa-arrow-up {
            color: #fff;
            font-size: 0px;
            transition: 0.5s;
        }
        &.on {
            opacity: 1;
            height: 52px;
            .fa-arrow-up {
                font-size: 30px;
            }
        }
    }
    .theme {
        background-color: #000;
        width: 52px;
        height: 52px;
        border-radius: 50%;
        border: none;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #000;
        box-shadow: 0px 10px 15px 0 rgba(0, 0, 0, 0.25);
        cursor: pointer;
        .fa-moon {
            color: #fff;
            font-size: 30px;
        }
        .fa-sun {
            color: #fff;
            font-size: 30px;
        }
    }
}
@media screen and (max-width: 1179px) {
    .btnsBox {
        width: 100%;
        right: 0;
        left: 0;
        display: flex;
        align-items: center;

        .up {
            width: 30px;

            .fa-arrow-up {
                font-size: 0px;
            }
            &.on {
                height: 30px;
                .fa-arrow-up {
                    font-size: 4vw;
                }
            }
        }
        .theme {
            width: 30px;
            height: 30px;

            .fa-moon {
                font-size: 4vw;
            }
            .fa-sun {
                font-size: 4vw;
            }
        }
    }
}
