footer {
	width: 100%;
	padding: 100px 0;
	background-color: #282c37;
	color: #fff;
	.inner {
		width: 1180px;
		margin: 0 auto;

		&::after {
			content: '';
			display: block;
			clear: both;
		}
		h1 {
			font: bold 30px/1 'Montserrat';
			text-align: center;
			margin-bottom: 40px;
			letter-spacing: 1px;
		}
		ul {
			display: flex;
			justify-content: center;
			margin-bottom: 20px;
			li {
				margin-right: 20px;
				text-align: center;
				a {
					font: 14px/1 'Montserrat';
					color: #ddd;
				}
			}
			span {
				margin-right: 20px;
				color: #ddd;
			}
		}
		p {
			font: 14px/1 'Montserrat';
			text-align: center;
		}
	}
}
@media screen and (max-width: 539px) {
	footer {
		padding: 20px 0;
		margin-top: 0;
		.inner {
			width: 100%;
			padding: 20px;
			h1 {
				font-size: 3.5vw;
				margin-bottom: 2vh;
			}
			ul {
				li {
					a {
						font-size: 2.5vw;
					}
				}
			}
			p {
				font-size: 2.3vw;
				line-height: 1.2;
				text-align: center;
				margin: 0 20px 0 20px;
			}
		}
	}
}
