section.data_page {
	width: 100%;
	padding: 200px 0;
	.inner {
		position: relative;
		width: 1180px;
		margin: 0 auto;
		display: flex;
		&::before {
			content: '';
			display: block;
			clear: both;
		}
		.left_side {
			width: 560px;
			float: left;
			margin-right: 20px;

			.main_text {
				width: 100%;

				h1 {
					margin: 50px;
					margin-top: 70px;
					font: bold 32px/1.3 'airal';
				}
				p {
					margin: 50px;
				}
			}

			.sub_text {
				width: 100%;
				.table {
					margin-top: 20px;
					p {
						margin-top: 10px;
						font: bold 18px/1 'arial';
						display: flex;
						justify-content: space-between;
						margin-right: 40px;
						margin-left: 50px;
					}
					.data {
						width: 470px;
						height: 10px;
						background-color: #e4e4e4;
						opacity: 0.6;
						span {
							display: block;
							width: 0%;
							height: 10px;
							background-color: rgb(241, 170, 38);
							transition: 3s;
						}
					}
					&:nth-of-type(2) {
						.data {
							span {
								width: 0%;
							}
						}
					}
					&:nth-of-type(3) {
						.data {
							span {
								width: 0%;
							}
						}
					}
				}
			}
		}
		.right_side {
			width: 560px;
			height: 560px;
			display: flex;
			flex-direction: column;
			opacity: 1;
			transition: 1s;
			.pic {
				width: 100%;
				height: 100%;
				display: flex;
				flex-direction: row;
				justify-content: center;
				align-items: center;

				.left,
				.right {
					width: 280px;
					height: 100%;
					display: flex;
					flex-direction: column;
					box-shadow: 0 0 16px 10px rgba(0 0 0 / 8%);

					article {
						width: 280px;
						height: 280px;
						display: flex;
						justify-content: center;
						align-items: center;
						flex-direction: column;
						font-size: 30px;
						.icons {
							width: 120px;
							height: 120px;
							display: flex;
							justify-content: center;
							align-items: center;
							border-radius: 50%;
							background-color: #6a88d8;
							margin-bottom: 20px;
							.fa-computer,
							.fa-id-card,
							.fa-graduation-cap {
								font-size: 80px;
								color: #fff;
							}
						}
						> p {
							font: bold 24px/1 'arial';
							text-align: center;
							opacity: 0;
							margin-top: 40px;
							transition: 1s 1s;
						}

						.name {
							width: 240px;
							height: 220px;
							border: 1px solid #6a88d8;
							border-radius: 20px;
							background-color: #6a88d8;
							display: flex;
							flex-direction: column;
							justify-content: center;
							align-items: center;
							position: relative;

							h2 {
								font: bold 26px/1 'arial';
								text-align: center;
								color: #fff;
								margin: 0;
							}
							p {
								font: 16px/1 'arial';
								text-align: center;
								margin: 20px;
								color: #fff;
							}
						}
					}
				}
				.left {
					margin-right: 20px;
					opacity: 0;
					margin-top: 120px;
					transition: 1s;
				}
				.right {
					margin-top: 180px;
					opacity: 0;
					transition: 1s 0.2s;
					article {
						.icons {
							transform: scale(1.2);
							margin-bottom: 40px;
						}
					}
				}
			}
		}
	}
	&.on {
		.inner {
			.left_side {
				.sub_text {
					.table {
						.data {
							span {
								width: 60%;
							}
						}
						&:nth-of-type(2) {
							.data {
								span {
									width: 80%;
								}
							}
						}
						&:nth-of-type(3) {
							.data {
								span {
									width: 70%;
								}
							}
						}
					}
				}
			}
			.right_side {
				.pic {
					.left,
					.right {
						article {
							> p {
								opacity: 1;
								margin-top: 0px;
							}
						}
					}
					.left {
						opacity: 1;
						margin-top: 0px;
					}
					.right {
						opacity: 1;
						margin-top: 60px;
					}
				}
			}
		}
	}
}

@media screen and (max-width: 539px) {
	section.data_page {
		width: 100%;
		padding: 100px 0;

		.inner {
			width: 100%;
			flex-direction: column;
			.left_side {
				width: 100%;
				.main_text {
					width: 100%;
					margin-bottom: 5vw;

					h1 {
						margin: 10vw 0 10px 30px;
						font: bold 6vw/1.3 'airal';
					}
					p {
						margin: 10px 10px 10px 30px;
					}
				}
				.sub_text {
					.table {
						p {
							margin-left: 30px;
							margin-right: 30px;
						}
						.data {
							width: 82%;
						}
					}
				}
			}
			.right_side {
				width: 100%;
				margin: 0 auto;
				.pic {
					.left,
					.right {
						width: 100%;
						box-shadow: none;
						margin: 0 auto;
						article {
							width: 80%;
							height: 100%;
							margin: 0 auto;
							.icons {
								width: 100px;
								height: 100px;
								.fa-computer,
								.fa-id-card,
								.fa-graduation-cap {
									font-size: 60px;
								}
							}
							p {
								font-size: 4vw;
								opacity: 1;
								margin-top: 0px;
							}
						}
					}
					.left {
						width: 40vw;
						opacity: 1;
						margin-top: 0px;
					}
					.right {
						width: 40vw;
						opacity: 1;
						margin-top: 0px;
						article {
							.icons {
								transform: scale(1);
								margin-bottom: 20px;
							}
							.name {
								width: 100px;
								height: 100px;
								border-radius: 50%;
								border: none;
								h2 {
									font-size: 6vw;
								}
								p {
									width: 160px;
									position: absolute;
									bottom: -60px;
									color: #000;
									margin: 0;
									font-size: 3vw;
									font-weight: bold;
								}
							}
							&:nth-of-type(2) {
								justify-content: center;
								align-items: center;
								transform: translateY(-25px);
							}
						}
					}
				}
			}
		}
		&.on {
			.inner {
				.right_side {
					.pic {
						.left,
						.right {
							margin: 0;
						}
					}
				}
			}
		}
	}
}
